autocomplete {
  display: inline-block;
}
autocomplete input {
  width: 100%;
  box-sizing: border-box;
  border: none;
}
autocomplete .suggestions {
  list-style-type: none;
  cursor: default;
  padding: 0;
  margin: 0;
  border: 1px solid #ccc;
  background: #fff;
  box-shadow: -1px 1px 3px rgba(0,0,0,0.1);
  position: absolute;
  z-index: 9999;
  max-height: 15rem;
  overflow: hidden;
  overflow-y: auto;
  box-sizing: border-box;
}
autocomplete .suggestion {
  padding: 0 0.3rem;
  line-height: 1.5rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #333;
}
autocomplete .suggestionhover,
autocomplete .suggestion.selected {
  background: #f0f0f0;
}
