nav {
  width: auto;
  max-width: 60em;
  float: none;
  display: block;
  margin-right: auto;
  margin-left: auto;
  padding-left: 0;
  padding-right: 0;
}
nav::after {
  content: '';
  display: table;
  clear: both;
}
nav ul.nav-list {
  list-style-type: none;
}
nav ul.nav-list li {
  float: left;
  clear: none;
  text-align: inherit;
  width: 31.33333333333333%;
  margin-left: 0%;
  margin-right: 3%;
  display: inline-block;
}
nav ul.nav-list li::after {
  content: '';
  display: table;
  clear: both;
}
nav ul.nav-list li:last-child {
  margin-right: 0%;
}
nav ul.nav-list li a {
  display: block;
  text-align: center;
  font-size: 14.201156945318074px;
  margin-top: 1.84844094752817em;
  line-height: 2.218129137033805em;
  margin-bottom: 0.369688189505634em;
  padding-top: 0;
  margin-top: 0 !important;
}
@media (min-width: 600px) {
  nav ul.nav-list li a {
    font-size: calc( 14.201156945318074px + (23.668594908863454 - 14.201156945318074) * ((100vw - 600px) / (1140 - 600)) );
  }
}
@media (min-width: 1140px) {
  nav ul.nav-list li a {
    font-size: 23.668594908863454px;
    margin-top: 1.84844094752817em;
    line-height: 2.218129137033805em;
    margin-bottom: 0.369688189505634em;
  }
}
