html {
  font-family: 'Space Mono', Menlo, Monaco, Consolas, "Courier New", monospace;
  font-weight: 300;
  color: #3a3c83;
  font-size: 16px;
  line-height: 1.75em;
}
@media (min-width: 600px) {
  html {
    font-size: calc( 16px + (32 - 16) * ((100vw - 600px) / (1200 - 600)) );
  }
}
@media (min-width: 1200px) {
  html {
    font-size: 32px;
  }
}
p,
blockquote,
pre,
address,
dl,
ol,
ul,
table {
  margin-bottom: 1.75em;
}
h1,
h2,
h3,
h4,
h5 {
