#text-editor {
  margin-top: 1rem;
}
.text-editor-container {
  width: auto;
  max-width: 60em;
  float: none;
  display: block;
  margin-right: auto;
  margin-left: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}
.text-editor-container::after {
  content: '';
  display: table;
  clear: both;
}
.text-editor-title {
  float: left;
  clear: none;
  text-align: inherit;
  width: 65.66666666666666%;
  margin-left: 0%;
  margin-right: 3%;
}
.text-editor-title::after {
  content: '';
  display: table;
  clear: both;
}
.text-editor-title:last-child {
  margin-right: 0%;
}
.text-editor-title h5 {
  border: none;
  font-weight: bold;
}
.text-editor-title h5::before {
  color: #c7c8e6;
}
.text-editor-avatar {
  float: left;
  clear: none;
  text-align: inherit;
  width: 31.33333333333333%;
  margin-left: 0%;
  margin-right: 3%;
}
.text-editor-avatar::after {
  content: '';
  display: table;
  clear: both;
}
.text-editor-avatar:last-child {
  margin-right: 0%;
}
.text-editor-avatar autocomplete {
  width: 100%;
  display: block;
}
.text-editor-avatar autocomplete input {
  font-size: 1em;
  border-right: 1px solid #3a3c83;
  border-left: 1px solid #3a3c83;
  padding: 0 0.2em;
  height: 1.4em;
}
.cm-editor .CodeMirror {
  font-size: 1em;
  line-height: 1.43em;
}
.cm-editor .CodeMirror .CodeMirror-linenumber {
  color: #000;
}
.cm-editor .CodeMirror .CodeMirror-linenumber:nth-of-type(4) {
  color: blu1;
}
