---
title: Cetered Container
name: container
category:
  - Structure
---

Description.

```html_example
<section class="container">This is a centered container block</section>
```
*/
.container {
  width: auto;
  max-width: 70em;
  float: none;
  display: block;
  margin-right: auto;
  margin-left: auto;
  padding-left: 1rem;
  padding-right: 1rem;
  float: left;
  clear: none;
  text-align: inherit;
  width: 100%;
  margin-left: 0%;
  margin-right: 3%;
}
.container::after {
  content: '';
  display: table;
  clear: both;
}
.container::after {
  content: '';
  display: table;
  clear: both;
}
.container:last-child {
  margin-right: 0%;
}
